$(document).ready(function() {
    $('.btn').on('click', function() {
        $('.btn').removeClass('selected');
        $(this).addClass('selected');
        $('.confirm').removeAttr('disabled');
        $('.confirm').removeClass('block');
        checkValue();
    });

    function checkValue() {
        $('.btn').each(function() {
            let buttonText = $(this).val();

            // Se o botão estiver selecionado, adiciona '- Selecionada'
            if ($(this).hasClass('selected')) {
                if (!buttonText.includes(' - Selecionada')) {
                    $(this).val(buttonText + ' - Selecionada');
                }
            } else {
                // Se não estiver selecionado, remove '- Selecionada' caso exista
                $(this).val(buttonText.replace(' - Selecionada', ''));
            }
        });
    }

    $('.confirm').on('click', function() {
        // Verifica se algum botão está selecionado e exibe o pop-up correspondente
        for (let i = 1; i <= 4; i++) {
            if ($('.btn' + i).hasClass('selected')) {
                $('#pop0' + i).removeClass('hide');
                scorm.saveObject(navigate.currentScreenUid, i);
                loadTela();
            }
        }
    });

    $('.rever').on('click', function() {
        // Verifica se algum botão está selecionado e exibe o pop-up correspondente
        for (let i = 1; i <= 4; i++) {
            if ($('.btn' + i).hasClass('selected')) {
                $('#pop0' + i).removeClass('hide');
            }
            // Seleciona o primeiro elemento filho com tabindex > 1 dentro do pop-up
            var firstTabIndexElement = $('#pop0' + i).find('[tabindex]').filter(function() {
                return $(this).attr('tabindex') > 1; // Filtra elementos com tabindex maior que 1
            }).first();
        
            // Verifica se o elemento existe antes de aplicar o foco
            if (firstTabIndexElement.length) {
                firstTabIndexElement.focus(); // Foca no primeiro elemento filho com tabindex > 1
            }
        }
    });

    $('.modal-feed .modal-close').on('click focus', function() {
        navigate.goto('pag04');
    });

    function loadTela() {
        const resposta = scorm.loadObject(navigate.currentScreenUid);

        if ( resposta ) {
            $('.confirm').addClass('hide');
            $('.rever').removeClass('hide block');
            $('.btn').css('pointer-events', 'none');
        }

        setTimeout(() => {
            if (resposta >= 1 && resposta <= 4) {
                // $('#pop0' + resposta).removeClass('hide'); // Exibe o pop-up correspondente
                $('.btn' + resposta).addClass('selected'); // Adiciona a classe 'selected' ao botão correspondente
                checkValue(); // Atualiza os valores dos botões
            }        
        }, 2000);
        
    }

    setTimeout(() => {
        loadTela();
    }, 300); // Mantém o setTimeout para garantir que a tela seja carregada após 300 ms
});
